export default {
	data() {
		return {
			message: '',
			type:'cart',
			pid:[],
			num:[],
			list:'',
			remark:'',
			url:'',
			orderid:'',
			new_url:'',
			show:'',
			state: 1,
			addr_id:'',
			uid:''
		};
	},
	methods: {
		add_address(){
			uni.navigateTo({
				url:'../../mine/setting/edit-address'
			})
		},
		//立即支付
		btn(){
			this.$store.dispatch('buyCommodity',{
				type:this.type,
				pid:this.pid,
				num:this.num,
				addr_id:uni.getStorageSync('addr_id'),
				remark:this.remark
			}).then(res=>{
				this.url = res.data.pay_url
				this.orderid = res.data.orderid
				this.new_url = this.url + "?id=" +this.orderid+"&uid="+this.uid
				uni.setStorageSync("payurl",this.new_url)
				uni.navigateTo({
					url:'../../webview/webview'
				})
			}).catch(err=>{})
		},
		//改变收货地址
		change(){
			uni.navigateTo({
				url:'../../mine/setting/address?state='+this.state
			})
		},
	},
	onLoad(e){
		let data = {
			'pid':this.pid,
			'num':this.num
		}
		this.type = e.type
		this.pid = e.pid.split(",")
		this.num = e.num.split(",")
		this.addr_id = e.id?e.id:''
		this.uid = e.uid
	},
	onShow() {
		this.$store.dispatch('getCartOrder',{
			type:this.type,
			pid:this.pid,
			num:this.num,
			addr_id:uni.getStorageSync('addr_id')
		}).then(res=>{
			this.list = res			
			if(this.list.addrlist.length == 0){
				this.show = false
			}else{
				this.show = true
			}
		}).catch(err=>{})
	}
}
